.button-round {
  cursor: pointer;
  width: 40px;
  height: 40px;
  background-color: #ff3e79;
  border-radius: 50%;
  border: none;
  outline: none;
  transition-property: transform box-shadow;
  transition-duration: 0.15s;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  -moz-column-gap: 0;
       column-gap: 0;
  row-gap: 0;
}

@-webkit-keyframes shake {
  0% {
    transform: rotate(0);
  }
  50% {
    transform: rotate(15deg);
  }
  75% {
    transform: rotate(-15deg);
  }
  100% {
    transform: rotate(0);
  }
}

@keyframes shake {
  0% {
    transform: rotate(0);
  }
  50% {
    transform: rotate(15deg);
  }
  75% {
    transform: rotate(-15deg);
  }
  100% {
    transform: rotate(0);
  }
}
.button-round:hover {
  -webkit-animation: shake 0.15s ease-in-out;
          animation: shake 0.15s ease-in-out;
}

.button-round:active {
